.home-screen {
  height: 688px;
  max-width: 100%;

  background-image: url("../images/main-car.png");
  background-repeat: no-repeat;
  background-size: cover;

  &__title {
    font-family: "Noto Sans", sans-serif;
    font-weight: 700;
    font-size: 50px;
    line-height: 68px;
    color: $background-color;

    margin-top: 118px;
    width: 715px;
  }

  &__subtitles {
    display: flex;
    gap: 20px;

    font-family: "Noto Sans", sans-serif;
    font-weight: 400;
    font-size: 15px;
    line-height: 20px;
    color: $background-color;

    &--first {
      width: 295px;
      margin-top: 32px;
    }

    &--second {
      width: 295px;
      margin-top: 32px;
    }
  }

  &--button {
    display: flex;
    justify-content: center;
    align-items: center;

    border: 2px solid $background-color !important;
    border-radius: 15px !important;
    background-color: inherit !important;

    font-family: "Noto Sans", sans-serif !important;
    font-weight: 500 !important;
    font-size: 20px !important;
    line-height: 27px !important;

    color: $background-color !important;
    width: 296px;
    height: 56px;

    cursor: pointer;
    margin-top: 72px;

    &:hover {
      background-color: $background-color !important;
      color: $text-color !important;
    }
    &:active {
      background-color: inherit !important;
      color: $background-color !important;
    }
    &:focus {
      box-shadow: none !important;
    }
    &:disabled {
      opacity: 0.4 !important;
    }
  }
}

.header {
  height: 72px;
  max-width: 1240px;
}

.nav {
  display: flex;
  justify-content: space-between;

  padding-top: 19px;

  &__logo {
    height: 35px;
    width: 139px;

    background-image: url("../images/header-logo.png");
    background-repeat: no-repeat;
    background-size: cover;

    cursor: pointer;
  }

  &__items {
    display: flex;
    align-items: center;

    font-family: "Nano-sans", sans-serif;
    font-weight: 500;
    color: $background-color;

    &-links {
      display: flex;

      margin-right: 45px;

      &__item {
        cursor: pointer;
        text-decoration: none;
        color: #f8f8f8;

        &:hover {
          color: #f8f8f8;
          text-decoration: underline;
        }
      }
      &__item:not(:last-child) {
        margin-right: 40px;
      }
    }

    &-facebook {
      width: 9px;
      height: 18px;

      background-image: url("../images/facebook.png");
      background-repeat: no-repeat;
      background-size: cover;

      margin-right: 31px;

      cursor: pointer;
    }
    &-instagram {
      width: 25px;
      height: 25px;

      background-image: url("../images/instagram.png");
      background-repeat: no-repeat;
      background-size: cover;

      cursor: pointer;
    }
  }
}
