.current-offers {
  max-width: 1240px;

  &__title {
    font-family: "Poppins", sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 50px;
    line-height: 75px;
    text-transform: uppercase;
    color: #aeaeae;

    margin-top: 98px;
    text-align: center;
  }

  &__filter {
    margin-top: 80px;
  }
}

.filter-selects {
  display: flex;
  justify-content: space-between;

  &__select {
    width: 170px !important;
    margin-right: 20px;

    border: 1px solid #bdbdbd !important;
    border-radius: 15px !important;

    &:focus {
      font-weight: 600;
      color: $text-color;

      box-shadow: none !important;
      background-image: none;
    }
    &:checked {
      border: 1px solid $brand-color !important;
    }
  }

  &__accept-btn {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 290px;
    height: 48px;

    background: $brand-color !important;
    border: 2px solid $brand-color !important;
    border-radius: 15px !important;

    font-family: "Noto Sans", sans-serif !important;
    font-style: normal !important;
    font-weight: 400 !important;
    font-size: 15px !important;
    line-height: 20px !important;

    color: $background-color !important;

    &:hover {
      color: $brand-color !important;
      background-color: inherit !important;
    }
    &:active {
      background: $brand-color !important;
      color: $background-color !important;
    }
    &:focus {
      box-shadow: none !important;

    }
    &:disabled {
      opacity: 0.4 !important;
    }
  }
}

.sort-vehicles {
  display: flex;
  justify-content: space-between;

  height: 16px;
  margin-top: 24px;

  &__available {
    label {
      width: 150px;
      height: 16px;

      font-family: "Noto Sans", sans-serif;
      font-weight: 400;
      font-size: 12px;
      line-height: 16px;

      color: $text-possible-option;
    }

    input {
      width: 16px;
      height: 16px;
      border: 1px solid $text-possible-option;

      &:checked {
        background: #df4e3c;
        box-shadow: none !important;
        border: 1px solid $brand-color !important;
      }
      &:focus {
        outline: none !important;
        box-shadow: none !important;
        border: 1px solid $text-possible-option !important;
      }
      &:active {
        background-color: inherit;
      }
      &:checked + label {
        color: $brand-color;
      }
    }

  }
}

.select__by {
  position: relative;

  &-header {
    display: flex;
    align-items: center;

    &-title {
      margin-right: 16px;
    }
  }

  &-current {
    cursor: pointer;
    font-family: "Noto Sans", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 16px;

    color: #8d8d8d;
    &:hover {
      color: #df4e3c;
    }
  }

  &-icon {
    height: 13px;
    width: 13px;

    margin-right: 8px;
  }

  &-body {
    display: none;
    width: 191px;
    position: absolute;
    right: 0;
    z-index: 1;

    background: #fff;
    border-radius: 15px;

    padding: 12px;
    &-item {
      white-space: nowrap;

      font-family: "Noto Sans", sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 16px;

      color: #000;
     &:not(:last-child) {
       margin-bottom: 12px;
     }
      &:hover {
        color: #df4e3c;
        cursor: pointer;
      }
    }
  }

  &--isActive {
    .select__by-body {
      display: block;
    }
  }
}

.cars-cards {
  display: flex;
  flex-wrap: wrap;
  gap: 20px;

  margin-top: 24px;
}

.card {
  display: block;
  width: 295px;
  height: 366px;

  background: #fff;
  box-shadow: 0 0 18px rgba(68, 68, 68, 0.09);

  &__image {
    position: relative;
    display: block;
    width: 295px;
    height: 200px;
  }

  &__title {
    margin-top: 20px;

    &__year {
      width: 37px;
      height: 26px;

      font-family: "Poppins", sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 17px;
      line-height: 26px;
      color: $text-possible-option;

      margin-left: 20px;
    }
    &__car-name {
      font-family: "Noto Sans", sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 17px;
      line-height: 23px;
      color: $text-color;
      cursor: pointer;

      margin-left: 8px;
    }
  }

  &__description {
    display: flex;

    margin-top: 16px;
    margin-left: 20px;

    &__km {
      font-family: "Noto Sans", sans-serif;
      font-style: normal;
      font-weight: 300;
      font-size: 14px;
      line-height: 19px;

      color: #8d8d8d;
    }
    &__vertical-line {
      width: 18px;
      height: 0;

      border: 1px solid #8d8d8d;
      opacity: 0.5;
      transform: rotate(90deg) translateX(8px);
    }
    &__transmission {
      font-family: "Noto Sans", sans-serif;
      font-style: normal;
      font-weight: 300;
      font-size: 14px;
      line-height: 19px;

      color: #8d8d8d;
    }
    &__fuel {
      font-family: "Noto Sans", sans-serif;
      font-style: normal;
      font-weight: 300;
      font-size: 14px;
      line-height: 19px;

      color: #8d8d8d;
    }
  }

  &__price {
    margin-left: 20px;
    margin-top: 26px;
    margin-bottom: 35px;

    font-family: "Noto Sans", sans-serif;
    font-style: normal;
    font-weight: 500;
    font-size: 20px;
    line-height: 27px;
    color: #000;
  }

  &:hover {
    .card__title__car-name {
      text-decoration: underline;
    }
  }

  &--new {
    &::after {
      content: "New";
      display: flex;
      justify-content: center;
      align-items: center;

      position: absolute;
      left: 0;
      top: 176px;

      width: 66px;
      height: 24px;
      background: $text-color;
      border-radius: 0 20px 20px 0;

      font-family: "Poppins", sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 18px;

      color: #fff;
    }
  }
  &--sold {
    &::after {
      content: "SOLD";
      position: absolute;
      width: 131px;
      height: 75px;
      left: 82px;
      top: 63px;

      font-family: "Poppins", sans-serif;
      font-style: normal;
      font-weight: 700;
      font-size: 50px;
      line-height: 75px;

      color: #fff;
    }
    .card__title,
    .card__description,
    .card__price {
      opacity: 0.5;
    }
  }
}
