.subscribe {
  display: flex;
  justify-content: space-between;
  max-width: 1240px;
  height: 174px;
  &__background {
    background-color: #000;
    box-shadow: 0 0 18px rgba(68, 68, 68, 0.09);
    margin-top: 100px;

  }

  &__left {
    &__title {
      width: 505px;
      height: 34px;

      font-family: "Noto Sans", sans-serif;
      font-style: normal;
      font-weight: 600;
      font-size: 25px;
      line-height: 34px;

      color: $background-color;

      margin-top: 40px;
    }
    &__subtitle {
      width: 505px;
      height: 40px;

      font-family: "Noto Sans", sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 15px;
      line-height: 20px;

      color: $background-color;

      margin-top: 20px;
    }
  }

  &__right {
    display: flex;
    align-items: center;
    &__input {
      height: 54px;

      border: 1px solid #bdbdbd !important;
      border-radius: 15px !important;
      background-color: inherit !important;

      color: #f8f8f8 !important;
      &:invalid {
        border: 1px solid #ff2307 !important;
      }

      &::placeholder {
        width: 473px;
        height: 20px;

        font-family: "Noto Sans", sans-serif !important;
        font-style: normal !important;
        font-weight: 400 !important;
        font-size: 15px !important;
        line-height: 20px !important;

        color: #f8f8f8 !important;
      }
      &:focus {
        box-shadow: none !important;
        &::placeholder {
          visibility: hidden;
        }
      }

      &-label {
        width: 505px;
      }
    }
  }

  &--button {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 190px;
    height: 54px;

    background: #f8f8f8 !important;
    border-color: #fff !important;
    border-radius: 15px !important;

    font-family: "Noto Sans", sans-serif !important;
    font-style: normal !important;
    font-weight: 400 !important;
    font-size: 15px !important;
    line-height: 20px !important;

    color: #000 !important;

    margin-left: 20px;

    &:hover {
      background-color: inherit !important;
      color: #fff !important;
    }
    &:active {
      background: #f8f8f8 !important;
      color: #000 !important;
    }
    &:focus {
      box-shadow: none !important;
    }
    &:disabled {
      opacity: 0.4 !important;
    }
  }
}
